webpackJsonp([187507352487630],{

/***/ 290:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"毫无准备，春招开始\"><a href=\"#%E6%AF%AB%E6%97%A0%E5%87%86%E5%A4%87%EF%BC%8C%E6%98%A5%E6%8B%9B%E5%BC%80%E5%A7%8B\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>毫无准备，春招开始</h2>\n<p>互联网的招聘时间，从来不按套路出牌，总是越早机会越多</p>\n<p>寒假还有在做学长的项目，但是基本已经进入收尾阶段，学完车之后便是咸鱼状态，以为暑期前5，6月份开始找实习就好。3月来到学校，马上就收到俱乐部的各种学长内推，一点准备也没有，就开始了面试</p>\n<p>结果无疑是很惨的，第一面就是阿里，当时阿里实习招聘已经接近尾声，面试官电话不能推到周末，只能当天抽空面试。面试官只用简单的几个问题便把我问到哑口无言</p>\n<ol>\n<li>\n<p>Q: 为什么选择前端？</p>\n<p>A: 前端变化日新月异，可以不断充电学习...</p>\n</li>\n<li>\n<p>Q: 那学习了啥？</p>\n<p>A: 懂点jQuery...（当时jQuery就会查API解决问题）</p>\n</li>\n<li>\n<p>Q: 打游戏吗？</p>\n<p>A: 不怎么打...</p>\n</li>\n<li>\n<p>Q: 那你不怎么打游戏，好像也没学到多少啊？真不打游戏？疑问脸 ):</p>\n<p>A: 真不打游戏...好吧确实没学到啥</p>\n</li>\n<li>\n<p>Q: JS里0.1 + 0.2 !== 0.3解释一下吧</p>\n<p>A: 嗯，应该浮点数精度取整的问题</p>\n</li>\n<li>\n<p>Q: 这谁都知道啊，你说具体点</p>\n<p>A: ...好吧当时确实说不清楚</p>\n</li>\n<li>\n<p>Q: 再问你一个吧，移动端点击事件300ms延迟怎么解决</p>\n<p>A: emmm...这个也不会</p>\n</li>\n<li>\n<p>Q: 有什么想问我的吗？</p>\n<p>我: 实习生培养政策是怎么样的？有mentor吗？</p>\n<p>面试官: 其实学东西还是要靠自己的</p>\n<p>我: 欧，好吧~</p>\n</li>\n</ol>\n<p>这次面试答得比较糟糕，其实第一个问题没答好，而且由于自己确实没学到什么，面试官对我第一印象就不太好，觉得我是个容易夸大自我的人吧，全程面试15min，可以说很凉了...</p>\n<p>第二次面试是头条，问了有20个题目左右，有4个左右没答上来，还有一个算法题压根没懂。当时还觉得应该有机会进下一轮，结果第二条就收到拒信，自己还是naive，不过宇宙条效率真的没得说</p>\n<p>3，4月份主要集中为一些大厂的面试，主要是老师或者俱乐部学长的内推，这期间自己也逐渐开始从0准备，并开始深挖自己的项目，前端也不止停留在用jQuery开发上，而是开始复习基础，React也开始跟了一个小小的项目，看官网的博客</p>\n<p>5月份开始，自己也在网上找合适的投递，那个时候就想找一个能学习React的公司，巧合下看到了Insta360的招聘，地点在深圳，使用React开发，一开始以为和360有什么联系，去查了下，发现公司的产品在国外市场做得很牛，便投递了</p>\n<p>约面试也很快，一面的时候有两个面试官，一个问了我JS基础，以及一些ES6的知识，答得不错，另外一个看了我项目跟我细聊，发现我还玩过Docker和Kubernetes，由于我对简历项目进行了深挖，基本原理都能说个一二，聊得还是挺满意的，而我自己对于愿意和你细聊项目，挖掘你长处的面试官也是挺有好感，包括秋招，拿到的几个offer，除了华为玄学点之外，其他几个面试时聊得都挺不错的</p>\n<p>没有意外，我收到了Insta360的实习offer，人生第一次看到offer letter，还是挺开心的</p>\n<h2 id=\"活力深圳，充满希望\"><a href=\"#%E6%B4%BB%E5%8A%9B%E6%B7%B1%E5%9C%B3%EF%BC%8C%E5%85%85%E6%BB%A1%E5%B8%8C%E6%9C%9B\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>活力深圳，充满希望</h2>\n<p>选择深圳作为人生第一站，家人也挺满意，因为真的很近啊，上大学35小时的火车，如今4小时左右的高铁</p>\n<p>一个没去过大城市的小子，提着行李箱来到高楼遍地的城市，满是诧异，刚下地铁我就开始拿高德导航，显示公司所在大楼只有200m，想想也有意思，当时自己开着导航，绕着公司大楼转悠一圈还没找到入口，而最无语的事情是，手机竟然被太阳晒花屏了Orz…便找了一个人问了下路，他给我指了下我面前这栋楼，说这栋就是啊。我也是苦笑不得</p>\n<p>由于手机花屏，不能联系上hr，只能登记后上了楼。当时自己还以为公司有自己的一栋楼，结果是两层，还好当时hr提前跟人打好招呼，我进去就被认了出来，见了下老大，找了个位置坐了下来</p>\n<p>第一次看互联网公司，原来真的和俱乐部的工位差不多，自己还以为会有一栋楼呢hhh</p>\n<p>hr找到我之后，先说了手机的事，她也无奈的笑了，联系我好久没联系上....</p>\n<p>那天下午我就坐在位置上，等待室友下班一起回去，安顿好行李，实习生活就要开始了</p>\n<h2 id=\"不断学习，不断充电\"><a href=\"#%E4%B8%8D%E6%96%AD%E5%AD%A6%E4%B9%A0%EF%BC%8C%E4%B8%8D%E6%96%AD%E5%85%85%E7%94%B5\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>不断学习，不断充电</h2>\n<p>Insta360我们组还是相对来说轻松的，公司也是6点下班，大小周这种。组里边基本没加过班，活儿也不算多</p>\n<p>刚入职的时候还是7月份，虽然秋招已经开始了，但是只是少部分公司的提前批，而且投递跨度也比较长，这个月我基本没投递公司，属于准备阶段，因此时间还是比较自由的，正好有一个室友办了健身卡，便邀约一起去健身了</p>\n<p>7月以及8月上旬白天工作，晚上健身房，还是安排得很舒服的，8月下旬以及9月之后，各种笔试面试安排就来了。那段时间作息也发生了改变，由于早上起得可以比较晚，晚上便也睡得晚，基本到凌晨2点以后，2点半左右了，最甚可以到3点。说起来也奇怪，根本没有睡意，不过做的都是自己的东西，兴趣爱好使然，还能满足成就感。唯一不能坚持的就是健身了，那个时候晚上7点左右约面试，一小时左右，面完8点才下去吃个饭，回来做做总结，已经没有去健身房的精力，而周末在宿舍独处就感觉很满足很惬意</p>\n<p>自己有个小毛病，也算一个缺点，一直很难克服，就是当有一个急切重要的目标没有完成时，便分散不出精力做其他的事了，哪怕是一直坚持的健身，可能也会放弃。在这里来看这件大事就是找工作，找不到工作时是焦虑的，拿到offer了，又还总想试试更好的，毕竟不像大佬们随随便便就是BAT，所以还是焦虑与期待并存的，精力过于集中便无法分散了。好在公司的事确实不算多，自己还有额外的时间学习新知识，不断充电，准备秋招</p>\n<p>下面回顾一下自己实习期间充的“电”</p>\n<p>在公司里一共做了三件工作</p>\n<ol>\n<li>部分商城活动页</li>\n<li>Chrome数据采集插件</li>\n<li>内部系统后台部分模块</li>\n</ol>\n<p>自己做了以下几件事</p>\n<ol>\n<li>搭建了博客系统</li>\n<li>产出10篇技术文章</li>\n<li>写了两个React开源小项目</li>\n</ol>\n<p>但是也有没做好的事</p>\n<ol>\n<li>Node没有花上时间学习</li>\n<li>算法题没有坚持刷到预期数目</li>\n<li>深入理解计算机系统和算法导论没有好好阅读</li>\n<li>两个小项目后期没有完善得很完美</li>\n<li>文章产出不算多（我的预期当时一周应该有2篇，远没达到）</li>\n</ol>\n<p>十月是一个艰苦的过程，倒不是面试，而是等待的过程，还好最终的结果算是满意。可能也是累了，已经咸鱼了这么久，接下来也要继续努力鸭~</p>\n<h2 id=\"脚踏实地，继续前行\"><a href=\"#%E8%84%9A%E8%B8%8F%E5%AE%9E%E5%9C%B0%EF%BC%8C%E7%BB%A7%E7%BB%AD%E5%89%8D%E8%A1%8C\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>脚踏实地，继续前行</h2>\n<p>如今可以算是一个社会人了，经过了深圳的实习，虽然自己很爱这座城市，但是最终经过选择还是没有留下来，也许将来会有机会回来看看</p>\n<p>Insta360确实是一家充满活力的公司，leader和周围同事的学识都给我留下了深刻的印象。当然最深的一起工作生活的室友，每天晚上一起熬夜，一起健身，一起吃饭。楼下常去的湖南粉馆，每天早上都要打个招呼买包子的老板。都是一笔珍贵的财富，我爱这的一切❤️</p>\n<p>网易云今天给我分享了一首歌，留在这里</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/blog/static/zaijian_netease-8744c601345e98728036e8a2312fbea9-dda94.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 400px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 152.75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"zaijian netease\"\n        title=\"\"\n        src=\"/blog/static/zaijian_netease-8744c601345e98728036e8a2312fbea9-dda94.jpg\"\n        srcset=\"/blog/static/zaijian_netease-8744c601345e98728036e8a2312fbea9-62b1e.jpg 213w,\n/blog/static/zaijian_netease-8744c601345e98728036e8a2312fbea9-dda94.jpg 400w\"\n        sizes=\"(max-width: 400px) 100vw, 400px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>脚踏实地，继续前行🏃</p>","frontmatter":{"title":"春招和深圳Insta360实习","date":"November 01, 2018","excerpt":"初入社会第一课，是一笔宝贵的财富","contents":[{"text":"毫无准备，春招开始","subContents":null},{"text":"活力深圳，充满希望","subContents":null},{"text":"不断学习，不断充电","subContents":null},{"text":"脚踏实地，继续前行","subContents":null}]}}},"pathContext":{"slug":"/life/2018/11/01/My-Internship-in-Insta360/"}}

/***/ })

});


